'use strict'

import { API, APIresponse } from './api'

export interface UserData {
	email: string
	password: string
}

/**
 *     API    .
 */
export class UserAPI {
	/**
	 *   .
	 * @param {UserData} userdata -  : email  .
	 * @returns {Promise<APIresponse>}  API,    ,  ,    `body`.
	 */
	async register(userdata: UserData): Promise<APIresponse> {
		return API.post('/auth/signup', {
			service: {
				email: userdata.email,
				password: userdata.password,
			},
		})
	}

	/**
	 *     .
	 * @param {UserData} userdata -   : email  .
	 * @returns {Promise<APIresponse>}  API.    `body`      .
	 */
	async login(userdata: UserData): Promise<APIresponse> {
		return API.post('/auth/login', {
			email: userdata.email,
			password: userdata.password,
		})
	}

	/**
	 *      (   ).
	 * @returns {Promise<APIresponse>}  API,   .
	 */
	async logout(): Promise<APIresponse> {
		return API.post('/auth/logout')
	}

	/**
	 *  access-   refresh-.
	 * @returns {Promise<APIresponse>}  API    (  `body.token`).
	 */
	async refresh(): Promise<APIresponse> {
		return API.post('/auth/refresh')
	}
}

/**
 *  API    .
 * @type {UserAPI}
 */
export const userApi = new UserAPI()
